import React from "react";
import { Select } from "antd";

export default function ErrorActionSelectBox(props) {
  return (
    <Select {...props} defaultValue={1}>
      <Select.Option key="0" value={0}>SilentlyContinue</Select.Option>
      <Select.Option key="1" value={1}>Stop</Select.Option>
      <Select.Option key="2" value={2}>Continue</Select.Option>
      <Select.Option key="3" value={3}>Inquire</Select.Option>
      <Select.Option key="4" value={4}>Ignore</Select.Option>
      <Select.Option key="5" value={5}>Suspend</Select.Option>
    </Select>
  );
}
